@import "global/01-mixins";
@import "fonts";
@import "global/03-vars";
html {
    box-sizing: border-box;
}

body {
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    line-height: 1;
    font-weight: 400;
    color: #252525;
    position: relative;
    height: 100%;
    background: #F4F6F6;
    -webkit-tap-highlight-color: transparent;
}

div,
section,
footer,
.btn {
    box-sizing: border-box;
}

a {
    color: #fff;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

a:focus,
input:focus {
    outline: none;
}

p {
    color: #252525;
    font-size: 20px;
    font-weight: 400;
    line-height: 29px;
    margin: 0 0 10px 0;
}

input {
    display: block;
    width: 100%;
    font-size: 30px;
    font-weight: 700;
    line-height: 29px;
    padding: 0 8px;
    margin-bottom: 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
}

input:hover,
input:focus {
    outline: none;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

ul li {
    padding: 0 0 0 0;
}

button {
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
}

.title {
    color: #fff;
    font-size: 42px;
    font-weight: 700;
    line-height: 53px;
    text-transform: uppercase;
    margin-bottom: 20px;
}

.desc {
    color: #252525;
    font-size: 20px;
    font-weight: 400;
    line-height: 29px;
    margin-bottom: 15px;
}

img {
    max-width: 100%;
    max-height: 100%;
    display: block;
}

.clearfix:after {
    content: '';
    display: table;
    clear: both;
}

.wrapper {
    position: relative;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.container {
    max-width: 1562px;
    width: 100%;
    padding: 0 16px;
    position: relative;
    margin: 0 auto;
}

.header {
    background: #fff;
    padding: 13px 8px 11px 67px;
}

.header-box {
    display: flex;
    align-items: center;
}

.logo {
    margin-right: 82px;
}

.menu {
    display: flex;
    justify-content: flex-start;
    li {
        margin-right: 73px;
    }
    li:last-child {
        margin-right: 0;
    }
    a {
        position: relative;
        font-weight: 500;
        font-size: 14px;
        line-height: 100%;
        text-align: center;
        letter-spacing: 0.22em;
        text-transform: uppercase;
        color: #878DA3;
    }
    a:hover {
        color: #424552;
    }
}

.profile {
    padding-right: 22px;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: auto;
    cursor: pointer;
}

.profile::after {
    content: '';
    display: block;
    position: absolute;
    top: 13px;
    right: -1px;
    width: 10px;
    height: 6px;
    background: url(../img/arr-down.svg) no-repeat center;
}

.profile-name {
    b {
        display: block;
        font-weight: normal;
        font-size: 14px;
        line-height: 100%;
        text-align: right;
        color: #424552;
        margin-bottom: 2px;
    }
    span {
        display: block;
        font-weight: normal;
        font-size: 11px;
        line-height: 100%;
        text-align: right;
        color: #929292;
    }
    margin-right: 15px;
}

.profile-image {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    background: blue;
}

.content-box {
    padding: 69px 0 0 0;
    h1 {
        margin-top: 0;
        font-weight: normal;
        font-size: 24px;
        line-height: 100%;
        color: #424552;
        margin-bottom: 30px;
    }
}

.content-box__top-line {
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.brn-abc {
    width: 35px;
    height: 34px;
    border-radius: 10px;
}


/* I E   S T Y L E S */

@media all and (-ms-high-contrast: none),
(-ms-high-contrast: active) {
    .block {
        width: 100%;
    }
}

.ov-hidden {
    overflow: hidden;
}

@media screen and (min-width:992px) {
    .btn-orange:hover {
        background: #ec4824;
    }
    .modal-close:hover {
        opacity: 0.7;
    }
}


/* 
    M O B I L E
*/

@media screen and (max-width: 1200px) {
    html,
    body {
        min-width: 320px;
        width: 100%;
        overflow-x: hidden;
    }
    a:focus {
        outline: none;
    }
}

